import React, { memo, forwardRef, ReactNode } from 'react';
import { mergeRestProps, MergePropsType } from '../../common';
import { ButtonWrap } from './styled';

export type IButtonProps = MergePropsType<{
  /**
   * 按钮类型
   */
  type?: 'normal' | 'primary' | 'danger' | 'text' | 'davinci' | 'sunk' | 'ghost';
  /**
   * 按钮尺寸
   */
  size?: 'small' | 'normal';
  /**
   * 按钮图标
   */
  icon?: ReactNode;
  /**
   * 按钮是否禁用
   */
  disabled?: boolean;
}, React.ButtonHTMLAttributes<HTMLButtonElement>>;

export const Button = memo(

  forwardRef<HTMLButtonElement, IButtonProps>(
    (props, ref) => {
      const {
        children,
        size,
        type = 'normal',
        icon,
        disabled,
        ...rest
      } = props;

      return (
        <ButtonWrap
          ref={ref}
          size={size}
          disabled={disabled}
          btnType={type}
          content={(icon && children) ? 'icon-text' : children ? 'only-text' : 'only-icon'}
          {...mergeRestProps(rest, 'wdn-button')}
        >
          {icon}
          {children}
        </ButtonWrap>
      );
    }),
);

Button.displayName = 'Button';